//
// Breadcrumb custom
// --------------------------------------------------

// Breadcrumb Classic
// -------------------------

.breadcrumb-classic {
  background: #890013;
  position: relative;
}

.breadcrumb-modern {
  background: $gray-darker;
}

.breadcrumb-classic,
.breadcrumb-modern {
  .list-inline-dashed {
    > li {
      &:last-child{
        color: rgba(255,255,255,.4);
        &:before{
          color: $white;
        }
      }
    }
  }
}

.breadcrumb-creative {
  text-align: left;
}

.breadcrumb-creative-panel {
  padding: 20px 0;
  border-bottom: 1px solid $gray-light;
}



