//
// Page loaders
// --------------------------------------------------

.page-loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  float: left;
  @include display-flex();
  @include flex-direction(column);
  @include justify-content(center);
  @include align-items(center);
  padding: 10px;
  z-index: 9999999;
  background: $white;
  transition: .3s all ease;

  &.loaded {
    opacity: 0;
    visibility: hidden;
    z-index: -1;
  }

  &.ending {
    display: none;
  }

  > * {
    position: relative;
    top: -40px;
  }
}

.page-loader-image {
  display: block;
  text-align: center;
}

.page-loader-body {
  margin-top: 90px;
  text-align: center;
}

.cssload-loader {
  display: block;
  margin:0 auto;
  width: 50px;
  height: 50px;
  position: relative;
  border: 6px solid $brand-primary;
  animation: cssload-loader 2.3s infinite ease;
}

.cssload-loader-inner {
  vertical-align: top;
  display: inline-block;
  width: 100%;
  background-color: $brand-primary;
  animation: cssload-loader-inner 2.3s infinite ease-in;
}





@keyframes cssload-loader {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(180deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes cssload-loader-inner {
  0% {
    height: 0;
  }

  25% {
    height: 0;
  }

  50% {
    height: 100%;
  }

  75% {
    height: 100%;
  }

  100% {
    height: 0;
  }
}