//
// RD Navbar
// --------------------------------------------------

$rd-navbar-fixed-height: 56px;

%rd-navbar-transition {
  transition: .3s all cubic-bezier(0.785, 0.135, 0.150, 0.860);
}

%rd-navbar-default-link-font {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
  font-family: $font-family-accent;
}

.rd-navbar {
  display: none;
  position: relative;
  z-index: 1030;
  text-align: left;

  ul {
    list-style-type: none;
    margin-bottom: 0;
  }

  a {
    transition: .3s all ease;
  }

  .responsive-tabs-classic {
    border-bottom: none;
    .resp-tab-content + .resp-accordion{
      border-top: none;
    }
  }
}

// Stuck menu
.rd-navbar-static.rd-navbar--is-stuck {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.rd-navbar-fixed,
.rd-navbar-static{
  display: block;
}

.rd-navbar--no-transition {
  &, *{
    transition: none!important;
  }
}

.rd-navbar-fixed-linked {

  @media (min-width: $screen-md-min) {
    .rd-navbar-absolute {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
    }
  }
}

.rd-navbar-static-linked {

  .rd-navbar-absolute {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}

.panel-title,
.rd-navbar-top-panel-toggle {
  display: none;
}

.rd-navbar-fixed {

  .panel-title,
  .rd-navbar-top-panel-toggle {
    display: inline-block;
  }

  .rd-navbar-megamenu .row {
    > [class*='col-'] {
      width: 100%;
      float: none;
    }
  }
}