//
// Flex Grid system
// --------------------------------------------------


// Base Grid Styles
// --------------------------------------------------

.shell,
.shell-wide,
.shell-fluid{
  @include make-shell($cell-spacing);
}

.shell,
.shell-wide{
  min-width: $shell-width;
  max-width: $shell-xs-width;

  @media (min-width: $screen-sm-min) {
    max-width: $shell-sm-width;
  }

  @media (min-width: $screen-md-min) {
    max-width: $shell-md-width;
  }

  @media (min-width: $screen-lg-min) {
    max-width: $shell-lg-width;
  }
}

.shell-wide {
  @media (min-width: $screen-xl-min) {
    max-width: $shell-xl-width;
  }
}

.range {
  @include make-range($cell-spacing);

  > .range{
    margin-left: 0;
    margin-right: 0;
  }
}

.range-spacer{
  @include flex-basis(100%);
}

[class*="cell-"] {
  @include make-cell-spacing($cell-spacing);
}

html.lt-ie-10 .range-condensed,
.range-condensed {
  margin-left: 0;
  margin-right: 0;

  > [class*='cell'] {
    padding-left: 0;
    padding-right: 0;
  }
}
html.lt-ie-10 .range-lg-condensed,
.range-lg-condensed {
  @media (min-width: $screen-lg-min) {
    margin-left: 0;
    margin-right: 0;

    > [class*='cell'] {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

html.lt-ie-10 .range-xl-condensed,
.range-xl-condensed {
  @media (min-width: $screen-xl-min) {
    margin-left: 0;
    margin-right: 0;

    > [class*='cell'] {
      padding-left: 0;
      padding-right: 0;
    }
  }
}


html.lt-ie-10 .range-narrow,
.range-narrow {
  @include make-range($cell-spacing-narrow);

  > [class*='cell'] {
    @include make-cell-spacing($cell-spacing-narrow);
  }

  > *  + [class*='cell'] {
    @include make-cell-spacing($cell-spacing-narrow);
  }
}


// Flex Grid Styles
// --------------------------------------------------

.range {
  @include display-flex();
  @include flex(0 1 auto);
  @include flex-direction(row);
  @include flex-wrap(wrap);

  > .range{
    @include flex-basis(100%);
  }
}

.range-vertical{
  @include flex-direction(column);
}

.range > [class*='cell'] {
  @include flex(0 0 auto);
  @include flex-basis(100%);
}

@each $resolution, $alias in ($screen-xs-min : xs, $screen-sm-min : sm, $screen-md-min : md, $screen-lg-min : lg, $screen-xl-min : xl) {
  @media (min-width: $resolution) {

    @include make-grid-system($alias, $cell-count);
  }
}

html.lt-ie-10 {
  .range > {
    @include make-fallback($cell-count);
  }
}

.range > {
  .cell-6 {
    flex-basis: 50%;
    max-width: 50%;
  }
}

.range-custom {
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
}

@media (min-width: $screen-lg-min) {
  .cell-lg-1-5 {
    flex-basis: 20%;
    max-width: 20%;
  }

  .range-lg-condensed {
    margin-left: 0;
    margin-right: 0;

    > [class*='cell'] {
      padding-left: 0;
      padding-right: 0;
    }
  }
}