/*
*
* Isotope
*/

$isotope-default-color: $gray-light;
$isotope-default-color-active: $brand-primary;
$isotope-default-border: $isotope-default-color-active;
$isotope-nav-spacing: 5px;

$isotope-inverse-color: $white;
$isotope-inverse-color-active: $brand-primary;
$isotope-inverse-border: $isotope-inverse-color-active;

//
// Isotope animations
//
@keyframes sprite-animation {
	from {
		background-position: 0 0;
	}
}

.isotope {
	z-index: 0;
	display: block;
	min-height: 160px;
	transition: .4s all ease;

	.row {
		margin-bottom: -30px;

		> [class*="col-"] {
			margin-top: 30px;
		}
	}

	.row-narrow {
		margin-bottom: -$grid-gutter-width-narrow;

		> [class*="col-"] {
			margin-top: $grid-gutter-width-narrow;
		}
	}

	.row-condensed {
		margin-bottom: 0;

		> [class*="col-"] {
			margin-top: 0;
		}
	}

	[class*="col-"] {
		display: block;
		will-change: transform;
		backface-visibility: hidden;
		transition: .1s opacity ease-in;

		@media (max-width: $screen-xxs-max) {
			width: 100%;
		}
	}

	&.isotope--loaded {
		[class*="col-"] {
			opacity: 1;
		}

		&:after {
			opacity: 0;
			visibility: hidden;
		}
	}
}

// Isotope defaults
.isotope-filters {
	$value: 10px;

	> * {
		margin-top: 0;
		vertical-align: middle;
	}

	.inline-list {
		position: relative;
		word-spacing: 0;

		li {
			display: inline-block;
		}

		a {
			position: relative;
			transition: .3s;
			color: $isotope-default-color;
		}

		.isotope-filters-trigger {
			display: none;
		}
	}
}

.isotope-condensed {
	margin-left: 0;
	margin-right: 0;
	.row > * {
		margin-top: -1px;
		margin-bottom: -1px;
	}
}

* + .isotope {
	margin-top: 0;
}

* + .isotope-condensed {
	margin-top: 30px;
}

@media (max-width: $screen-sm-max) {
	.isotope-condensed {
		padding: 0 0 30px;
		margin-left: 0;
		margin-right: 0;
		.row.row-condensed > [class*='col'] {
			padding-left: 10px;
			padding-right: 10px;
		}

		.row.row-condensed > [class*='col'] + [class*='col'] {
			margin-top: 25px;
		}

	}
}

@media (min-width: $screen-sm-min) {
	* + .isotope {
		margin-top: 28px;
	}
	* + .isotope-condensed {
		margin-top: 50px;
	}
}
