//
// Owl Carousel
// --------------------------------------------------


// Owl Carousel - Animate Plugin
// -------------------------

.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

// Owl Carousel - Auto Height Plugin
// -------------------------

.owl-height {
  -webkit-transition: height 500ms ease-in-out;
  -moz-transition: height 500ms ease-in-out;
  -ms-transition: height 500ms ease-in-out;
  -o-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out;
}

// Core Owl Carousel CSS File
// -------------------------

.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  //position: relative;
  z-index: 1;
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  text-align: center;
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel .owl-refresh .owl-item {
  display: none;
}

.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

// No Js
//
.no-js .owl-carousel {
  display: block;
}

// Owl Carousel - Lazy Load Plugin
// -------------------------

.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}

// Owl Carousel Video Plugin
// -------------------------

.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  font: 400 40px/80px 'FontAwesome';
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -webkit-transition: scale 100ms ease;
  -moz-transition: scale 100ms ease;
  -ms-transition: scale 100ms ease;
  -o-transition: scale 100ms ease;
  transition: scale 100ms ease;
  &:before {
    content: '\f144';
  }
}

.owl-carousel .owl-video-play-icon:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}

// Owl Carousel Default
// -------------------------

.owl-carousel-default {
  .owl-prev,
  .owl-next {
    position: absolute;
    top: 50%;
    transition: .3s;
    z-index: 1;
    margin-top: -25px;
    width: 50px;
    height: 50px;
    line-height: 46px;
    font-size: 36px;
    text-align: center;
    border-radius: 50%;
    color: $white;

    &:hover {
      background: rgba(0,0,0,.3);
    }
    &:before{
      position: relative;
    }
  }

  // Owl Dots
  .owl-dots {
    margin-top: 40px;
    margin-left: -20px;
    line-height: 8px;
    text-align: center;
    padding-bottom: 20px;
  }
  .owl-dot {
    margin-left: 20px;
    display: inline-block;
    width: 10px;
    height: 10px;
    line-height: 8px;
    border-radius: 50%;
    background: $gray;
    transition: 0.35s all ease;
    &.active {
      position: relative;
      @include transform(scale(1.6));
      background: $brand-primary;
    }
    &:hover{
      @include transform(scale(1.6));
    }
  }
  .owl-next {
    right: 20px;
    &:before{
      right: -2px;
    }
  }

  .owl-prev {
    left: 20px;
    &:before{
      left: -2px;
    }
  }

  .tablet & {
    .owl-dots {
      display: block !important;
    }
    .owl-nav {
      display: none !important;
    }
  }
}
.owl-carousel-default.owl-carousel-dots-lg-left{
  // Owl Dots
  .owl-dots {
    text-align: center;
    @media (min-width: $screen-lg-min) {
      text-align: left;
    }
  }
}
@include rules-responsive(veil, $medias, owl-dots) {
  .owl-dots {
    display: none !important;
  }
}

@include rules-responsive(reveal, $medias, owl-dots) {
  .owl-dots {
    display: block !important;
  }
}

@include rules-responsive(veil, $medias, owl-nav) {
  .owl-nav {
    display: none !important;
  }
}

@include rules-responsive(reveal, $medias, owl-nav) {
  .owl-nav {
    display: block !important;
  }
}

.owl-carousel.carousel-type-1 {
  .quote-classic-boxed q {
    font-style: normal;
  }
  
}

.owl-prev,
.owl-next {
  cursor: pointer;
}