//
// Offsets
// --------------------------------------------------


// Insets
// -------------------------

$insets: (0, 10px, 15px, 20px, 25px, 30px, 38px, 50px, 60px, 70px, 100px, 110px);

html .page{
  @include indent-responsive(inset, left, padding-left, $medias, $insets);
  @include indent-responsive(inset, right, padding-right, $medias, $insets);
}


// Elements
// -------------------------

.inset-left-7p{
  padding-left: 7%;
}
.inset-right-7p{
  padding-right: 7%;
}

.inset-left-11p{
  padding-left: 11%;
}

.inset-right-11p{
  padding-right: 11%;
}


// Grid
// -------------------------


// Sections
// -------------------------


// Responsive
// -------------------------

$offsets: (0, 5px, 10px, 12px, 15px, 17px, 20px, 22px, 25px, 27px, 30px, 35px, 40px, 45px, 47px, 50px, 56px, 60px, 65px, 70px, 85px, 90px, 110px, 114px, 133px, 147px);

html .page{
  @include indent-responsive(offset, top, margin-top, $medias, $offsets);
}

html {
  // Range spacing
  .range-0 {
    @include grid-offset(0px);
  }

  .range-10 {
    @include grid-offset(10px);
  }

  .range-12 {
    @include grid-offset(12px);
  }

  .range-15 {
    @include grid-offset(15px);
  }

  .range-20 {
    @include grid-offset(20px);
  }

  .range-30 {
    @include grid-offset(30px);
  }

  .range-50 {
    @include grid-offset(50px);
  }

  .range-60 {
    @include grid-offset(60px);
  }

  .range-65 {
    @include grid-offset(65px);
  }

  .range-70 {
    @include grid-offset(70px);
  }

  .range-85 {
    @include grid-offset(85px);
  }

  @media (min-width: $screen-sm-min) {

    .range-sm-30 {
      @include grid-offset(30px);
    }

    .range-sm-45 {
      @include grid-offset(45px);
    }
  }

  @media (min-width: $screen-lg-min) {
    .range-lg-0 {
      @include grid-offset(0);
    }

    .range-lg-80 {
      @include grid-offset(80px);
    }
  }
}