// Element groups
// --------------------------------------------------

html{
  .group{
    @include group(15px, 15px);
  }

  .group-xs{
    @include group(5px, 5px);
  }

  .group-sm{
    @include group(10px, 10px);
  }

  .group-lg{
    @include group(20px, 20px);
  }

  .group-xl{
    @include group(30px, 20px);
  }

  .group-middle {
    display: inline-flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .group-bottom{
    display: inline-flex;
    align-items: flex-end;
  }
}